.pokemon-type-container-small {
  display: flex;
  text-align: center;
}

.pokemon-type-container-large {
  display: flex;
  width: 100%;
  height: 30%;
}

.pokemon-types-card-small {
  border-radius: 3px;
  line-height: 18px;
  max-width: 110px;
  margin: 2% 1.5625% 0 0;
  width: 38.4375%;
  float: left;
  text-transform: capitalize;
  font-size: 11px;
  width: 100%;
  font-family: "Exo", sans-serif;
  height: 21px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pokemon-types-card-large {
  text-transform: capitalize;
  overflow: auto;
  width: 32%;
  height: 32px;
  border-radius: 5px;
  font-family: "Exo", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  overflow: auto;
  margin-right: 2%;
  margin-bottom: 2%;
  padding-top: 1%;
  padding-bottom: 1%;
}

.pokemon-type-title {
  width: 90%;
  font-size: 125%;
  font-family: "Exo";
  font-weight: 500;
}

.grass {
  background-color: #9bcc50;
  color: #212121;
}

.poison {
  background-color: #b97fc9;
  color: white;
}

.fire {
  background-color: #fd7d24;
  color: white;
}

.water {
  background-color: #4592c4;
  color: white;
}

.flying {
  background: linear-gradient(180deg, #3dc7ef 50%, #bdb9b8 50%);
  color: #212121;
}

.bug {
  background-color: #729f3f;
  color: white;
}

.normal {
  background-color: #a4acaf;
  color: #212121;
}

.electric {
  background-color: #eed535;
  color: #212121;
}

.ground {
  background: linear-gradient(180deg, #f7de3f 50%, #ab9842 50%);
  color: #212121;
}

.fairy {
  background-color: #fdb9e9;
  color: #212121;
}

.fighting {
  background-color: #d56723;
  color: white;
}

.psychic {
  background-color: #f366b9;
  color: white;
}

.rock {
  background-color: #a38c21;
  color: white;
}

.steel {
  background-color: #9eb7b8;
  color: #212121;
}

.ice {
  background-color: #51c4e7;
  color: #212121;
}

.ghost {
  background-color: #7b62a3;
  color: white;
}

.dragon {
  background: linear-gradient(180deg, #53a4cf 50%, #f16e57 50%);
  color: white;
}

.dark {
  background-color: #707070;
  color: white;
}

@media (min-width: 900px) {
  .pokemon-types-card-large {
    padding: 0;
  }
}
